.dino-selection {
  flex-direction: column;
  align-items: center;
  height: 440px;
  margin-top: 4em;
  display: flex;
  position: relative;
}

.dino-selection ul {
  scroll-behavior: smooth;
  flex-direction: column;
  gap: 9px;
  max-height: 345px;
  margin: 40px 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.dino-selection ul li {
  list-style: none;
}

.dino-selection ul li svg {
  width: 81px;
  height: 73px;
  transition: filter .3s ease-in-out;
}

.dino-selection ul li:hover svg path, .dino-selection ul li.active svg path {
  fill: #79ffff;
  stroke: #79ffff;
}

.dino-selection ul li:hover svg ellipse, .dino-selection ul li.active svg ellipse {
  stroke: #79ffff;
}

.dino-selection .arrow-up, .dino-selection .arrow-down {
  cursor: pointer;
  z-index: 10;
  width: 40px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.dino-selection .arrow-up {
  margin-left: -40px;
  top: 0;
  rotate: 180deg;
}

.dino-selection .arrow-down {
  bottom: 0;
}

.selected-icon {
  margin: 0;
}

.selected-icon h3 {
  text-align: center;
}

.selected-icon svg path {
  fill: #79ffff;
  stroke: #79ffff;
  width: 60px;
}

.selected-icon svg ellipse {
  stroke: #79ffff;
}

.dino_img {
  width: 80%;
}

.dangerous-warning {
  text-align: center;
  color: #f4fa36;
  font-size: 18px;
  font-weight: bolder;
}

@media screen and (width >= 720px) {
  .dino-icon {
    width: 93px;
    height: 83px;
  }

  .dino-selection {
    gap: 0;
    height: auto;
    margin-top: -.5em;
    padding: 0 10px;
    display: grid;
  }

  .dino-selection ul {
    max-height: unset;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    padding: 0;
    display: grid;
    overflow: visible;
  }

  .dino-selection li {
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;
    display: flex;
  }

  .arrow-up, .arrow-down {
    display: none;
  }
}

@media screen and (width >= 1200px) {
  .dino-selection ul {
    grid-template-columns: repeat(4, 1fr);
    display: grid;
  }
}

.hidden {
  display: none;
}

.mobile-hide {
  visibility: hidden;
}

@media screen and (width >= 720px) {
  .mobile-hide {
    visibility: visible;
  }
}
